import React, { Component } from "react";
import Chart from "chart.js";

import "./SkillsMore.css";
import "./ui.css";

class SkillsMore extends Component {
  constructor(props) {
    super(props);
  }

  componentDidUpdate() {
    let ctx = document.getElementById("chart-area");
    var myChart = new Chart(ctx, {
      type: "pie",
      data: {
        datasets: [
          {
            label: "Usage Precent",
            data: [55, 70, 100, 85, 23, 44],
            backgroundColor: [
              "rgba(227, 76, 38, 1)",
              "rgba(38, 77, 228, 1)",
              "rgba(240, 219, 79, 1)",
              "rgba(97, 219, 251, 1)",
              "rgba(118, 74, 188, 1)",
              "rgba(86, 61, 124, 1)",
            ],
            borderColor: [
              "rgba(227, 76, 38, 0.7)",
              "rgba(38, 77, 228, 0.7)",
              "rgba(240, 219, 79, 0.7)",
              "rgba(97, 219, 251, 0.7)",
              "rgba(118, 74, 188, 0.7)",
              "rgba(86, 61, 124, 0.7)",
            ],
            borderWidth: 1,
          },
        ],
        labels: ["HTML", "CSS", "Javascript", "React", "Redux", "Bootstrap"],
      },
      options: {
        maintainAspectRatio: false,
        responsive: true,
        title: {
          display: true,
          position: "top",
          text: "Usage Percentage",
        },
        legend: {
          position: "bottom",
        },
      },
    });
  }

  render() {
    return (
      <div className={this.props.newClass}>
        <button className='Skills-Button' onClick={this.props.handleClick}>
          More info{" "}
          <i className={this.props.isHidden ? "arrow down" : "arrow up"}></i>
        </button>
        <div id='SkillsMore-canvas-holder'>
          <canvas id='chart-area' width='500' height='500'></canvas>
        </div>
      </div>
    );
  }
}

export default SkillsMore;
