import React, { Component } from "react";
import "./SkillsIcon.css";

class SkillsIcon extends Component {
  render() {
    return (
      <img
        className={this.props.className}
        src={this.props.src}
        alt={this.props.alt}
        style={this.props.style}
      />
    );
  }
}

export default SkillsIcon;
