import React, { Component } from "react";
import html from "./img/html-5.svg";
import css from "./img/css-3.svg";
import firebase from "./img/firebase.svg";
import git from "./img/git-icon.svg";
import bootstrap from "./img/bootstrap.svg";
import javascript from "./img/javascript.svg";
import node from "./img/nodejs.svg";
import reactlogo from "./img/react.svg";
import redux from "./img/redux.svg";

import "./Skills.css";

import SkillsIcon from "./SkillsIcon";
import Slider from "./Slider";
import SkillsMore from "./SkillsMore";

class Skills extends Component {
  static defaultProps = {
    icons: [html, css, javascript, reactlogo, redux, bootstrap, git, node],
  };
  constructor(props) {
    super(props);
    this.state = {
      isHidden: true,
      items: [...document.getElementsByClassName("SkillsIcon")],
      icons: this.props.icons,
    };

    this.handleMore = this.handleMore.bind(this);
  }

  renderElement() {
    let itemSpacing = document.body.clientWidth / this.state.icons.length;

    return (
      <Slider>
        {this.state.icons.map((icon, i) => {
          return (
            <SkillsIcon
              className='SkillsIcon'
              src={icon}
              alt={icon}
              style={{
                margin: `${itemSpacing / this.state.icons.length}px`,
              }}
            />
          );
        })}
      </Slider>
    );
  }

  handleMore(e) {
    this.setState(state => ({ isHidden: !state.isHidden }));
  }

  render() {
    let moreClass = this.state.isHidden
      ? "SkillsMore More-Hide"
      : "SkillsMore More-Show";
    let arrowClass = this.state.isHidden ? "arrow up" : "arrow down";
    return (
      <div className='Skills'>
        {this.renderElement()}
        <SkillsMore
          newClass={moreClass}
          handleClick={this.handleMore}
          isHidden={this.state.isHidden}
        />
      </div>
    );
  }
}

export default Skills;
