import React, { Component } from "react";

import "./TemplateInfo.css";

export default class TemplateInfo extends Component {
  render() {
    return (
      <div className={this.props.className}>
        <p>
          Templates are for you and your business website or personal website!
        </p>
        <p>What YOU get with Templates?</p>
        <ol className='TemplateInfoLeft'>
          <li>Faster Development</li>
          <li>Easy to Maintain Website</li>
          <li>Lower Cost</li>
        </ol>
      </div>
    );
  }
}
