import React, { Component } from "react";
import uuid from "uuid/dist/v4";
import ravangradlogo from "./img/logo.svg";

import ProjectsSingle from "./Projects-Single";
import "./Projects.css";

class Projects extends Component {
  static defaultProps = {
    projects: [
      {
        id: uuid(),
        logo: ravangradlogo,
        name: "Ravangrad Brewery",
        description: "First Craft Brewery in city of Sombor",
        status: "inProgress",
        url: "http://www.ravangradpivara.rs/",
      },
    ],
  };
  constructor(props) {
    super(props);
  }

  render() {
    let p = this.props.projects;
    return (
      <div className='Projects'>
        <h1 className='Projects-Heading'>Projects</h1>
        {p.map(p => (
          <ProjectsSingle
            id={p.id}
            logo={ravangradlogo}
            name={p.name}
            desc={p.description}
            status={p.status}
            url={p.url}
          />
        ))}
      </div>
    );
  }
}

export default Projects;
