import React, { Component } from "react";

import "./Projects-Single.css";
import ravangradlogo from "./img/logo.svg";

class ProjectSingle extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div id={this.props.id} className='Projects-Single'>
        <div className='InnerProjects-Single'>
          <img src={ravangradlogo} alt={this.props.name + "logo"} />
          <h3>{this.props.name}</h3>
          <p>{this.props.desc}</p>
          <p>
            Status: <span>{this.props.status}</span>
          </p>
          <a
            target='_blank'
            className='Projects-Single-anchor'
            href={this.props.url}>
            GO!
          </a>
        </div>
      </div>
    );
  }
}

export default ProjectSingle;
