import React, { Component } from "react";
import "./Navbar.css";
import { NavLink } from "react-router-dom";
import Head from "./Head";

class Navbar extends Component {
  render() {
    return (
      <nav className='Navbar'>
        <NavLink
          exact
          activeClassName='Navbar-active'
          className='active'
          to='/'>
          Home
        </NavLink>
        <NavLink exact activeClassName='Navbar-active' to='#bio'>
          Bio
        </NavLink>
        <NavLink exact activeClassName='Navbar-active' to='/skills'>
          Skills
        </NavLink>
        <NavLink exact activeClassName='Navbar-active' to='/projects'>
          Projects
        </NavLink>
      </nav>
    );
  }
}

export default Navbar;
