import React, { Component } from "react";
import TemplateInfo from "./TemplateInfo";

import "./Templates.css";
import "./ui.css";

class Templates extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tempShow: true,
    };

    this.handleMore = this.handleMore.bind(this);
  }
  handleMore(e) {
    this.setState({ tempShow: !this.state.tempShow });
  }
  render() {
    return (
      <div className='Templates'>
        <h1>Templates</h1>
        <h1>&lt;ComingSoon /&gt;</h1>
        <button onClick={this.handleMore}>
          Learn More <i className='arrow down'></i>
        </button>
        <TemplateInfo
          className={
            this.state.tempShow
              ? "TemplateInfo TempHide"
              : "TemplateInfo TempShow"
          }
        />
      </div>
    );
  }
}

export default Templates;
