import React, { Component } from "react";
import "./Bio.css";

class Bio extends Component {
  render() {
    return (
      <div className='Bio' id='#bio'>
        <p>
          I am a self-taught Front-end Developer, with a long history of coding.
          I started with Web development in the middle of year 2019. Since then
          I learnt and refined my skills.
          <br />
          <br />
          When I first got in touch with coding was around 10-15 years ago when
          I was trying to create a Counter-Strike 1.6 Zombie Plugin with php, a
          few years later I tried creating my own Video Game in Unity 3D, back
          then Unity used Unityscript(fancy name for Javascript) and I was
          adopting skills in it. After that I tought to myself coding is not for
          me and started Music Production.
          <br />
          <br /> Since then year 2019 came and I decided that, with my knowledge
          of C programming language in high school, and how I managed to create
          stuff in Unity, that I will learn and get stuff done!
          <br />
          <br />
          If you have any questions send me an email!
        </p>
      </div>
    );
  }
}

export default Bio;
